.heading-3xWQN,
.heading-1Dm1Y {
  position: relative;
  display: flex;
  align-items: center;
  margin: 10px 0 40px;
  width: 100%;
  white-space: nowrap;
  font-size: 32px;
}
.heading-3xWQN:before,
.heading-1Dm1Y:before {
  counter-increment: section;
  content: "0" counter(section) ".";
  margin-right: 10px;
  font-family: var(--font-SFMono);
  font-weight: normal;
  color: var(--c-green);
  font-size: var(--fs-xl);
  position: relative;
  bottom: 4px;
}
.heading-3xWQN:after,
.heading-1Dm1Y:after {
  content: "";
  display: block;
  height: 1px;
  width: 300px;
  background-color: var(--bg-c-medium-grey);
  position: relative;
  top: -5px;
  margin-left: 20px;
}
.heading-1Dm1Y {
  display: block;
  color: var(--c-green);
  font-size: var(--fs-md);
  font-family: var(--font-SFMono);
  font-weight: normal;
  margin-bottom: 20px;
  justify-content: center;
}
.heading-1Dm1Y:before {
  bottom: 0;
  font-size: var(--fs-sm);
}
.heading-1Dm1Y:after {
  display: none;
}
@media (max-width: 1000px) {
  .heading-3xWQN:after {
    width: 200px;
  }
  .heading-1Dm1Y {
    font-size: var(--fs-sm);
  }
  .heading-1Dm1Y:before {
    font-size: var(--fs-smish);
  }
}
@media (max-width: 768px) {
  .heading-3xWQN {
    font-size: 24px;
  }
  .heading-3xWQN:before {
    font-size: var(--fs-lg);
  }
  .heading-3xWQN:after {
    width: 100%;
  }
}
@media (max-width: 600px) {
  .heading-3xWQN:after {
    margin-left: 10px;
  }
}
